* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #abbaab;
  background: -webkit-linear-gradient(to right, #fff, #abbaab);
  color: #212121;
  background: linear-gradient(to right, #fff, rgba(38, 215, 228, .576));
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.country-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  display: flex;
}

@media screen and (min-width: 1000px) {
  .country-container {
    padding: 50px;
  }
}

#search-box {
  font: inherit;
  width: 100%;
  cursor: pointer;
  border: none;
  border-radius: 15px;
  margin-bottom: 25px;
  padding: 15px;
  transition: all .25s linear;
}

#search-box:hover, #search-box:focus {
  box-shadow: 4px 4px 8px rgba(34, 60, 80, .2);
}

@media screen and (min-width: 1000px) {
  #search-box {
    width: 883px;
  }
}

.country-list li {
  width: 100%;
  background-color: #fff;
  border: 1px solid gray;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  transition: all .25s linear;
  display: flex;
}

@media screen and (min-width: 1000px) {
  .country-list li {
    width: 883px;
    flex-direction: unset;
  }
}

.country-list li:not(:last-child) {
  margin-bottom: 20px;
}

.country-list p {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.3;
}

@media screen and (min-width: 1000px) {
  .country-list p {
    font-size: 25px;
  }
}

.country-list img {
  width: 160px;
  margin-bottom: 20px;
}

@media screen and (min-width: 1000px) {
  .country-list img {
    margin-bottom: 0;
    margin-right: 30px;
  }
}

.country-info {
  flex-direction: column;
  display: flex;
}

.country-info p {
  font-size: 24px;
  line-height: 1.3;
}

.country-info p:not(:last-child) {
  margin-bottom: 10px;
}

.country-info span {
  color: rgba(66, 20, 233, .79);
  margin-top: 15px;
  margin-bottom: 15px;
  font-size: 40px;
  display: block;
}

.country-info li {
  background-color: #fff;
  border: 1px solid gray;
  border-radius: 20px;
  padding: 20px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

p, h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

button {
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

